<template>
<div>
   
   <div id="fms" style="margin:0px auto; width: 884px; height: 580px; margin-top:10px;">
      
   </div>
    
</div>

</template>

<script>
export default {
   
   mounted () {
      console.log('montado')
     $f("fms", "./../../static/flowplayer/flowplayer-3.2.11.swf", {

         clip: {
               url: 'j8gz-9fr9-vtwr-4a7t',
               provider: 'rtmp',
               live: true,
               autoplay: true
         },

         plugins: {
               rtmp: {
                  url: './../../static/flowplayer/flowplayer.rtmp-3.2.10.swf',
                  netConnectionUrl: 'rtmp://hasrv.chaco.gov.ar/oflaDemo'
               }
         }
      }); 
   },
   destroyed () {
      console.log('destruido')
   }
}


</script>


